function HeaderTitleSectionComponent() {
  const imageSrc =
    "https://images.unsplash.com/photo-1587614297696-d150ef162d88?ixid=MnwxMjA3fDB8MHxzZWFyY2h8NXx8Z2lybCUyMGNvbXB1dGVyfGVufDB8fDB8fA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=600&q=60";
  return (
    <div className="header-title-section">
      <div className="header-title-section-flex">
        <div className="header-title-container">
          <h1 className="header-title">
            Your Next <br /> Online{" "}
            <span className="header-title-style">School</span>
          </h1>
          <div className="header-desc">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit.
            Pellentesque hendrerit hendrerit euismod.
          </div>
          <div className="header-search-bar">
            <div className="header-search-bar-input-group">
              <div className="header-search-bar-field">
                <i className="material-icons">search</i>
                <span>Course title or keyword</span>
              </div>
              <div className="header-search-bar-divider" />
              <div className="header-search-bar-dropdown">
                <span>Categories</span>
                <i className="material-icons">expand_more</i>
              </div>
            </div>
            <div className="header-search-bar-button">Search</div>
          </div>
        </div>
      </div>
      <div
        className="header-image"
        style={{ backgroundImage: "url(" + imageSrc + ")" }}
      />
    </div>
  );
}

export default HeaderTitleSectionComponent;
