import HeaderCompaniesComponent from "./components/HeaderCompaniesComponent";
import HeaderTitleSectionComponent from "./components/HeaderTitleSectionComponent";
import NavComponent from "./components/NavComponent";
import PopularSubjectSectionComponent from "./components/PopularSubjectSectionComponent";

function HomePage() {
  return (
    <>
      <header className="header">
        <NavComponent />
        <HeaderTitleSectionComponent />
        <HeaderCompaniesComponent />
      </header>
      <PopularSubjectSectionComponent />
    </>
  );
}

export default HomePage;
