import React from "react";

function HeaderCompaniesComponent() {
  return (
    <div className="header-companies">
      <div className="header-companies-container">
        <div className="header-companies-total">
          <div className="header-companies-total-value">1.3k</div>
          <div className="header-companies-total-title">Trusted Companies</div>
        </div>
        <div className="header-companies-divider" />
        <div className="header-companies-logo-group">
          <img
            className="header-companies-logo"
            src="https://www.udemy.com/staticx/udemy/js/webpack/udemy-pride-logo.d1b003c808a949847374f9a4267e1053.svg"
          />
          <img
            className="header-companies-logo"
            src="https://upload.wikimedia.org/wikipedia/en/3/32/Coursera_logo.svg"
          />
          <img
            className="header-companies-logo"
            src="https://www.edx.org/images/logos/edx-logo-elm.svg"
          />
          <img
            className="header-companies-logo"
            src="https://upload.wikimedia.org/wikipedia/commons/6/6c/Codecademy.svg"
          />
        </div>
      </div>
    </div>
  );
}

export default HeaderCompaniesComponent;
